<template>
  <div>


    <div style="padding: 10px 0">
      <el-input
          v-model="name"
          placeholder="请输入名称"
          style="width: 200px"
          suffix-icon="el-icon-search"
      ></el-input>
      <el-button style="margin-left: 5px" type="primary" @click="load"
      >搜索</el-button
      >
      <el-button style="margin-left: 5px" type="warning" @click="reset"
      >重置</el-button
      >
    </div>

    <div>
      <el-button style="margin: 5px" type="primary" @click="handleAdd()">新增<i class="el-icon-circle-plus"></i></el-button>
      <el-popconfirm
          cancel-button-text="取消"
          class="ml-5"
          confirm-button-text="确定"
          icon="el-icon-info"
          icon-color="red"
          title="确定批量删除这些数据吗"
          @confirm="delBatch"
      >
        <el-button slot="reference" style="margin: 5px" type="danger">批量删除<i class="el-icon-remove"></i></el-button>
      </el-popconfirm>
      <el-upload
          :action="'http://'+serverIP+'/menu/import'"  style="display: inline-block"
          :show-file-list="false"
          :headers="headersObj"
          :accept="'xlsx'"
          :on-success="handleExcelSuccess"
      >
        <el-button style="margin: 5px" type="primary" >导入<i class="el-icon-download"></i></el-button>
      </el-upload>

      <el-button style="margin: 5px" type="primary" @click="exp">导出<i class="el-icon-upload2"></i></el-button>
    </div>

    <el-table
        :data="tableData"
        border
        row-key="id"
        default-expand-all
        stripe
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column label="ID" prop="id" width="60"> </el-table-column>
      <el-table-column label="名称" prop="name" width="140">
      </el-table-column>
      <el-table-column label="路径" prop="path"></el-table-column>
      <el-table-column label="页面路径" prop="pagePath"></el-table-column>

      <el-table-column label="图标" prop="icon" align="center">
        <template slot-scope="scope">
          <i :class="scope.row.icon" style="font-size: 20px;" ></i>
        </template>
      </el-table-column>
      <el-table-column label="描述" prop="description" width="140"> </el-table-column>
      <el-table-column label="操作" width="auto" align="center">
        <template slot-scope="scope" >
          <el-button type="primary" @click="handleAdd(scope.row.id)" v-if="!scope.row.pid&&!scope.row.path"> <i class="el-icon-plus"></i>新增子菜单</el-button>
          <el-button type="success" @click="handleEdit(scope.row)"
          >编辑<i class="el-icon-edit-outline"></i
          ></el-button>

          <el-popconfirm
              cancel-button-text="取消"
              class="ml-5"
              confirm-button-text="确定"
              icon="el-icon-info"
              icon-color="red"
              title="确定删除吗"
              @confirm="handleDel(scope.row)"
          >
            <el-button  type="danger" slot="reference"
            >删除<i class="el-icon-document-delete"></i
            ></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>


    <!--新增窗口-->
    <el-dialog
        :visible.sync="dialogFormVisible"
        title="菜单信息"
        width="30%"
    >
      <el-form label-width="80px" size="small">
        <el-form-item label="名称">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="路径">
          <el-input v-model="form.path" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="页面路径">
          <el-input v-model="form.pagePath" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="图标">
          <el-select clearable v-model="form.icon" placeholder="请选择" style="width: 100%">
            <el-option v-for="items in options" :key="items.name" :label="items.name" :value="items.value">
              <i :class="items.value">   {{items.name}}</i>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="form.description" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>



  </div>
</template>

<script>
import {serverIP} from "../../public/config";
export default {
  name: "User",
  data(){
    return{
      serverIP:serverIP,
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
      tableData: [],
      total: 0,
      pageNum: 1,
      pageSize: 10,
      name: "",
      dialogFormVisible: false,
      form: {},
      multipleSelection: [],
      options:[]

    }
  },
  created() {
    //请求分页查询数据
    this.load();
  },
  computed:{
    headersObj(){
      const token = this.user.token
      return {
        token: token
      }
    }
  },
  methods: {
    load() {
      this.request
          .get("/menu", {
            params: {
              name: this.name,
            },
          })
          .then((res) => {
            console.log(res);
            this.tableData = res.data
          });

    },
    handleSizeChange(pageSize) {
      console.log(pageSize);
      this.pageSize = pageSize;
      this.load();
    },
    handelCurrentChange(pageNum) {
      console.log(pageNum);
      this.pageNum = pageNum;
      this.load();
    },
    reset() {
      this.Name = "";
      this.load();
    },
    handleAdd(pid) {
      this.request.get("/dict/getIcon").then(res=>{
        if (res.code==='200'){
          this.options=res.data
        }
      })
      this.form = {};
      this.dialogFormVisible = true;
      if(pid){
        this.form.pid=pid
      }
    },
    save() {
      this.request.post("/menu", this.form).then((res) => {
        console.log(this.form);
        if (res.code === '200') {
          this.$message.success(res.msg)
          this.dialogFormVisible = false;
          this.load();
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    handleEdit(row) {
      this.request.get("/dict/getIcon").then(res=>{
         if (res.code==='200'){
           this.options=res.data
         }
      })
      this.dialogFormVisible = true;
      this.form = row;
    },
    handleDel(row) {
      this.request.delete("/menu/" + row.id).then((res) => {
        if (res.code === '200') {
          this.$message.success("删除成功");
          this.load();
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    delBatch() {
      //确认是否删除
      this.$confirm('是否删除选中文件?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let ids= this.multipleSelection.map(item=>item.id)
        if (ids.length===0){
          this.$message.error("请选择要删除的文件")
          return
        }
        this.request.delete('/file/del/batch',{
          data:ids
        }).then(res=>{
          this.$message.success("删除成功")
          this.load()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    exp() {
      window.open(`http://${serverIP}/menu/export`);
    },
    handleExcelSuccess(res) {
      if (res.code === '200') {
        this.$message.success("导入成功");
        this.load();
      } else {
        this.$message.error(res.msg);
      }
    },
  },
}
</script>

<style scoped>

</style>